<template>
  <view class="page">
    <view class="container">
      <!-- È°µÈù¢Ê†áÈ¢ò -->
      <view class="page-header">
        <text class="header-icon">üìä</text>
        <text class="header-title">ÂØºÂá∫Êä•Ë°®</text>
        <text class="header-desc">ÈÄâÊã©Êä•Ë°®Á±ªÂûãÂíåÊó∂Èó¥ËåÉÂõ¥ËøõË°åÂØºÂá∫</text>
      </view>

      <!-- Êä•Ë°®Á±ªÂûãÈÄâÊã© -->
      <view class="section">
        <view class="section-title">
          <text class="title-icon">üìã</text>
          <text>Êä•Ë°®Á±ªÂûã</text>
        </view>
        <view class="report-types">
          <view
            v-for="type in reportTypes"
            :key="type.value"
            class="type-item"
            :class="{ 'type-active': selectedType === type.value }"
            @click="selectType(type.value)"
          >
            <text class="type-icon">{{ type.icon }}</text>
            <view class="type-content">
              <text class="type-name">{{ type.name }}</text>
              <text class="type-desc">{{ type.desc }}</text>
            </view>
            <view class="type-check" v-if="selectedType === type.value">
              <text class="check-icon">‚úì</text>
            </view>
          </view>
        </view>
      </view>

      <!-- Êó•ÊúüËåÉÂõ¥ÈÄâÊã© -->
      <view class="section">
        <view class="section-title">
          <text class="title-icon">üìÖ</text>
          <text>Êó∂Èó¥ËåÉÂõ¥</text>
        </view>

        <!-- Âø´Êç∑ÈÄâÊã© -->
        <view class="quick-date">
          <view
            v-for="quick in quickDates"
            :key="quick.value"
            class="quick-item"
            :class="{ 'quick-active': selectedQuick === quick.value }"
            @click="selectQuickDate(quick.value)"
          >
            <text>{{ quick.label }}</text>
          </view>
        </view>

        <!-- Ëá™ÂÆö‰πâÊó•Êúü -->
        <view class="custom-date">
          <view class="date-row">
            <text class="date-label">ÂºÄÂßãÊó•Êúü</text>
            <picker mode="date" :value="startDate" @change="onStartDateChange">
              <view class="date-picker">
                <text>{{ startDate || 'ËØ∑ÈÄâÊã©' }}</text>
                <text class="picker-icon">üìÖ</text>
              </view>
            </picker>
          </view>
          <view class="date-row">
            <text class="date-label">ÁªìÊùüÊó•Êúü</text>
            <picker mode="date" :value="endDate" @change="onEndDateChange">
              <view class="date-picker">
                <text>{{ endDate || 'ËØ∑ÈÄâÊã©' }}</text>
                <text class="picker-icon">üìÖ</text>
              </view>
            </picker>
          </view>
        </view>
      </view>

      <!-- ÂØºÂá∫ËØ¥Êòé -->
      <view class="tips-section">
        <view class="tip-item">
          <text class="tip-icon">üí°</text>
          <text class="tip-text">ÂØºÂá∫ÁöÑExcelÊñá‰ª∂Â∞Ü‰∏ãËΩΩÂà∞Êú¨Âú∞ÔºåËØ∑Ê≥®ÊÑèÊü•Êî∂</text>
        </view>
        <view class="tip-item">
          <text class="tip-icon">‚ö†Ô∏è</text>
          <text class="tip-text">Â§ßÈáèÊï∞ÊçÆÂØºÂá∫ÂèØËÉΩÈúÄË¶ÅËæÉÈïøÊó∂Èó¥ÔºåËØ∑ËÄêÂøÉÁ≠âÂæÖ</text>
        </view>
      </view>

      <!-- ÂØºÂá∫ÊåâÈíÆ -->
      <view class="export-btn-wrapper">
        <button
          class="export-btn"
          :disabled="!canExport"
          @click="handleExport"
          hover-class="button-hover"
        >
          <text>Á´ãÂç≥ÂØºÂá∫</text>
        </button>
      </view>
    </view>
  </view>
</template>

<script>
import { exportReport } from '../../api/export.js';

export default {
  data() {
    return {
      selectedType: 1, // ÈªòËÆ§ÈÄâÊã©‰ºöÂëòÂç°Êä•Ë°®
      selectedQuick: 'today', // ÈªòËÆ§ÈÄâÊã©‰ªäÊó•
      startDate: '',
      endDate: '',
      reportTypes: [
        {
          value: 1,
          name: '‰ºöÂëòÂç°Êï∞ÊçÆ',
          desc: 'ÊâÄÊúâÁî®Êà∑ÁöÑ‰ºöÂëòÂç°‰ø°ÊÅØ',
          icon: 'üé´'
        },
        {
          value: 2,
          name: 'ÂÖëÊç¢Á†ÅÊï∞ÊçÆ',
          desc: 'ÊâÄÊúâÁî®Êà∑ÁöÑÂÖëÊç¢Á†Å‰ø°ÊÅØ',
          icon: 'üéÅ'
        },
        {
          value: 3,
          name: 'ÂÖëÊç¢ËÆ∞ÂΩïÊï∞ÊçÆ',
          desc: 'ÊâÄÊúâÁî®Êà∑ÁöÑÂÖëÊç¢ËÆ∞ÂΩï',
          icon: 'üìù'
        },
        {
          value: 4,
          name: 'ËÆ¢ÂçïÂÖ•Ë¥¶Êï∞ÊçÆ',
          desc: 'Â∑≤ÊîØ‰ªò‰∏îÊú™ÈÄÄÊ¨æÁöÑËÆ¢Âçï',
          icon: 'üí∞'
        }
      ],
      quickDates: [
        { value: 'today', label: 'Êú¨Êó•' },
        { value: 'week', label: 'Êú¨Âë®' },
        { value: 'month', label: 'Êú¨Êúà' }
      ]
    }
  },
  computed: {
    canExport() {
      return this.selectedType && this.startDate && this.endDate;
    }
  },
  onLoad() {
    // ÈªòËÆ§ÈÄâÊã©‰ªäÊó•
    this.selectQuickDate('today');
  },
  methods: {
    // ÈÄâÊã©Êä•Ë°®Á±ªÂûã
    selectType(value) {
      this.selectedType = value;
    },

    // ÈÄâÊã©Âø´Êç∑Êó•Êúü
    selectQuickDate(value) {
      this.selectedQuick = value;
      const today = new Date();
      let start, end;

      switch (value) {
        case 'today':
          start = end = this.formatDate(today);
          break;
        case 'week':
          // Êú¨Âë®‰∏ÄÂà∞‰ªäÂ§©
          const day = today.getDay() || 7; // Âë®Êó•‰∏∫0ÔºåËΩ¨Êç¢‰∏∫7
          const weekStart = new Date(today);
          weekStart.setDate(today.getDate() - day + 1);
          start = this.formatDate(weekStart);
          end = this.formatDate(today);
          break;
        case 'month':
          // Êú¨Êúà1Âè∑Âà∞‰ªäÂ§©
          start = this.formatDate(new Date(today.getFullYear(), today.getMonth(), 1));
          end = this.formatDate(today);
          break;
      }

      this.startDate = start;
      this.endDate = end;
    },

    // ÂºÄÂßãÊó•ÊúüÊîπÂèò
    onStartDateChange(e) {
      this.startDate = e.detail.value;
      this.selectedQuick = ''; // Ê∏ÖÈô§Âø´Êç∑ÈÄâÊã©
    },

    // ÁªìÊùüÊó•ÊúüÊîπÂèò
    onEndDateChange(e) {
      this.endDate = e.detail.value;
      this.selectedQuick = ''; // Ê∏ÖÈô§Âø´Êç∑ÈÄâÊã©
    },

    // Ê†ºÂºèÂåñÊó•Êúü
    formatDate(date) {
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },

    // ÂØºÂá∫Êä•Ë°®
    async handleExport() {
      if (!this.canExport) {
        uni.showToast({
          title: 'ËØ∑ÈÄâÊã©Êä•Ë°®Á±ªÂûãÂíåÊó∂Èó¥ËåÉÂõ¥',
          icon: 'none'
        });
        return;
      }

      // È™åËØÅÊó•ÊúüËåÉÂõ¥
      if (new Date(this.startDate) > new Date(this.endDate)) {
        uni.showToast({
          title: 'ÂºÄÂßãÊó•Êúü‰∏çËÉΩÂ§ß‰∫éÁªìÊùüÊó•Êúü',
          icon: 'none'
        });
        return;
      }

      try {
        uni.showLoading({
          title: 'ÂØºÂá∫‰∏≠...',
          mask: true
        });

        const reportType = this.selectedType;
        const reportName = this.reportTypes.find(t => t.value === reportType)?.name || 'Êä•Ë°®';

        // Ë∞ÉÁî®ÂØºÂá∫Êé•Âè£
        // res ÊòØ uni.request ÁöÑÂéüÂßãÂìçÂ∫îÂØπË±°
        const res = await exportReport({
          reportType: reportType,
          startDate: this.startDate,
          endDate: this.endDate
        });

        uni.hideLoading();

        if (res.statusCode === 200) {
          // Ëé∑ÂèñÊñá‰ª∂Âêç
          let fileName = `${reportName}.xlsx`;
          const disposition = res.header['Content-Disposition'] || res.header['content-disposition'];
          if (disposition && disposition.indexOf('filename*=') !== -1) {
             // filename*=UTF-8''...
             const match = disposition.match(/filename\*=UTF-8''(.+)/);
             if (match && match[1]) {
                fileName = decodeURIComponent(match[1]);
             }
          } else if (disposition && disposition.indexOf('filename=') !== -1) {
             const match = disposition.match(/filename="?([^";]+)"?/);
             if (match && match[1]) {
                fileName = match[1];
             }
          }

          // #ifdef H5
          // H5ÁéØÂ¢É
          const blob = new Blob([res.data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
          const url = window.URL.createObjectURL(blob);
          const link = document.createElement('a');
          link.href = url;
          link.download = fileName;
          link.style.display = 'none';
          document.body.appendChild(link);
          link.click();
          window.URL.revokeObjectURL(url);
          document.body.removeChild(link);

          uni.showToast({
            title: 'ÂØºÂá∫ÊàêÂäü',
            icon: 'success'
          });
          // #endif

          // #ifndef H5
          // Â∞èÁ®ãÂ∫è/AppÁéØÂ¢É
          const fs = uni.getFileSystemManager();
          // ‰ΩøÁî® wx.env.USER_DATA_PATH (Â∞èÁ®ãÂ∫è) Êàñ _doc (App)
          // ÂÖºÂÆπÂ§ÑÁêÜ
          let basePath;
          if (typeof wx !== 'undefined' && wx.env && wx.env.USER_DATA_PATH) {
            basePath = wx.env.USER_DATA_PATH;
          } else {
            basePath = uni.env.USER_DATA_PATH;
          }

          const filePath = `${basePath}/${fileName}`;

          fs.writeFile({
            filePath: filePath,
            data: res.data,
            encoding: 'binary',
            success: () => {
              uni.openDocument({
                filePath: filePath,
                showMenu: true,
                fileType: 'xlsx',
                success: () => {
                  uni.showToast({
                    title: 'ÂØºÂá∫ÊàêÂäü',
                    icon: 'success'
                  });
                },
                fail: (err) => {
                  console.error('ÊâìÂºÄÊñáÊ°£Â§±Ë¥•', err);
                  uni.showToast({
                    title: 'ÊâìÂºÄÊñáÊ°£Â§±Ë¥•',
                    icon: 'none'
                  });
                }
              });
            },
            fail: (err) => {
              console.error('ÂÜôÂÖ•Êñá‰ª∂Â§±Ë¥•', err);
              uni.showToast({
                title: '‰øùÂ≠òÊñá‰ª∂Â§±Ë¥•',
                icon: 'none'
              });
            }
          });
          // #endif
        } else {
            // ÈîôËØØÂ§ÑÁêÜÂ∑≤Âú® download Â∞ÅË£Ö‰∏≠Â§ÑÁêÜÈÉ®ÂàÜÔºå‰ΩÜËøôÈáåÂèØ‰ª•ÂÜçÊ¨°ÊèêÁ§∫
            uni.showToast({
              title: 'ÂØºÂá∫Â§±Ë¥•',
              icon: 'none'
            });
        }
      } catch (error) {
        uni.hideLoading();
        console.error('ÂØºÂá∫Â§±Ë¥•:', error);
        // ÈîôËØØ‰ø°ÊÅØÂ∑≤Âú® request.js ‰∏≠ÊèêÁ§∫
      }
    }
  }
}
</script>

